@import url(https://fonts.googleapis.com/css2?family=Zen+Tokyo+Zoo&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fira+Sans:wght@200&family=Zen+Tokyo+Zoo&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fira+Sans:wght@200&family=Zen+Tokyo+Zoo&display=swap);

* {
    margin:0;
}

.app{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    padding: 20px;
    background-image: linear-gradient( 181deg,  rgba(225,109,245,1) 6.9%, rgba(78,248,231,1) 112.8% );
}

.header{
    display: -webkit-flex;
    display: flex;  
    -webkit-justify-content: space-between;  
            justify-content: space-between;  
    margin-bottom: 20px;    
    -webkit-align-items: center;    
            align-items: center;   
}

.heading{
    font-family: 'Zen Tokyo Zoo';
    color:red;
}

.para{
    margin-top: -2%;
}

.dropdown{
    background-color:cyan;
}


.app_left{
     -webkit-flex:0.9 1;
             flex:0.9 1;                /* left part take the 90% of total width */
}

.app_car{
    width:94%;
    margin-top: 0.05%;
    margin-left:3%; 
    margin-right:3%;
    height: 100%;
}

.car_bg{
    background-color: #EAE2B6;
}

.images{
 height: 520px;
}

.g{
    margin-top: 10px;
}



.world_img{
    width: 30px;
    padding-right:6px ;
}

.country_img{
    width:40px;
    padding-right:6px ;
}

.inside_menu{
    margin-left: 10px;
    width:40px;
    height:40px;
}

.graph-heading{
    margin-bottom: 10px;
}

.Cards{
    display:-webkit-flex;
    display:flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    margin: 0.1% 0.1%;
    height:-webkit-fit-content;
    height:-moz-fit-content;
    height:fit-content;
    
}
.Card_bg{
     background-color: #AAD8D3;
}




 .links{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
}




@media (max-width:940px) {
      .app{
          -webkit-flex-direction: column;
                  flex-direction: column;
      }

      .heading{
          font-size: 120%;
      }
      .para{
          font-size: 40%;
          margin-top: -10%;
          
      }
      .info_stats{
          -webkit-flex-direction: column;
                  flex-direction: column;
        
      }
      .MuiCard-root{
          margin-bottom: 12px;
      }
      .info-Box{
        margin-right: 15px;
        margin-left: 15px;
    }

      .map{
          margin-bottom: 30px;
      }

      .app_car{
          width:100%;
          margin: auto;
      }
      .images{
          height:50%;
      }
      .car_h3,.car_p{
          font-size: 60%;
         
      }
      .car_h3,.car_p{
          margin-bottom:-1%;
      }

      .Cards{
          -webkit-flex-direction: column;
                  flex-direction: column;
          -webkit-align-items: center;
                  align-items: center;
      }
      .Card{
          margin-top: 3%;
      }
       
    }
.info_stats{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.info-Box{
    -webkit-flex:1 1;
            flex:1 1;
    cursor: pointer;
}

.info-Box:not(:last-child){
    margin-right: 15px;
}


.info-Box--selected {
    border-top: 10px solid yellowgreen !important;
}

.info-Box--red {
    border-color:  red !important;
}

.info-cases--green{
    color: lightgreen !important;
}


.info-Cases{
    color:red;
    font-weight: 600;
    font-size: 1.75rem;
    margin-bottom: 0.5rem;
}

.info-Total{
    color:grey;
    font-weight: 700;
    font-size: 0.8rem;
    margin-bottom: 10px;

}
.map{
  height:500px;
  background-color: white;
  padding: 1rem;
  border-radius: 20px;
  margin-top: 55px;
  box-shadow: 0 0 8px -4px rgba(0,0,0,0.5);
}

.map .leaflet-container{
  height:100%;
}

.info_name{
  font-size: 20px;
  font-weight: bold;
  color:grey;
}


.info_flag{
  height:100px;
  width:100%;
  background-size: cover;
  border-radius: 8px;
}

.info_cases,
.info_recovered,
.info_deaths,
.info_active,
.info_pop{
  font-size: 16px;
  margin-top: 5px;
}
.Live_cases{
    margin-top: 20px;
    overflow: scroll;
    height: 400px;
    background-color: white;

}
 .Live_cases tr:nth-of-type(odd){
     background-color:#BBBBBB;
 }

 .Live_cases tr{
     display: -webkit-flex;
     display: flex;
     -webkit-justify-content: space-between;
             justify-content: space-between;
 }

 .Live_cases td{
     padding: 0.5rem;
 }
.Card_img{
    width: 100%;
}

.img{
    height:100%;
}
.text-center{
    background-color:#F4C983;
}
.text-muted{
    background-color:#CCFFBD;
}

.showintable {
    width: 90%;
    height: auto;
    border: 1px solid black;
    margin: 10px;
    display: -webkit-flex;
    display: flex;
    padding: 10px;
    font-family: 'Courier New', Courier, monospace;
  }
  
  .showintable h3 {
    margin: 20px;
  }
